.container__profile__thisuser{
  max-height: 100%;
  max-width: 750px;
  margin: 30px 5px 0 15px;
  padding: 10px;
  background-color: rgba(0,0,0,0.3);
  color: #bbbbbb;
  border-radius: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  word-wrap: break-word;
}
.profile__ava_and_name_and_status{
  width: 100%;
  display: flex;
  justify-content: space-between;

}

.mainPhoto{
  min-width: 130px;
  display: flex;
  justify-content: center;
  cursor: pointer;
  overflow: hidden;
}
.mainPhoto img {
    border: 5px #491a0c solid;
    max-width: 110px;
    border-radius: 50%;
}
.name__status{
  text-align: right;
  max-width: 30vh;
  overflow: hidden;
}
.status__profile_info{
  cursor: pointer;
  color:#bbbbbb ;
}

input[type="file"] {
    position: absolute;
    left: 0;
    opacity: 0;
    top: 0;
    bottom: 0;
    width: 100%;
  }
  
.input__down {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 100%;
  }
.label__down {
  border: #bbbbbb 3px solid;
  display: inline-block;
  position: relative;
  height: 119px;
  width: 119px;
  border-radius: 50%;
  }

.label__down:hover{opacity: 0.7; transition: 0.7s;}
.label__down:hover::after {content: "\21BB"; font-size: 30px; color: #491a0c; position: absolute; top: 35%; left: 35%;  }


.descriptionBlock {
  margin-top: 30px;
  width: 100%;
  height: 100%;
  color: #bbbbbb;

}
@media screen and (max-width: 750px) {
  .container__profile__thisuser{
    background-color: #6b311a ;  
  }
  .container__profile__thisuser{
    margin: 30px 0 0 0;
  }
}